/**
 * @typedef {object} TiledItem
 * @property {string} id
 * @property {object} injected
 * @property {array} injected.list
 * @property {string} injected.lastmod
 * @property {function} injected.loadMore
 * @property {boolean} injected.canLoop
 * @property {number} width Real width of the item.
 * @property {number} height Real height of the item.
 * @property {Vue.Component} renderComponent The Vue component used to render the item.
 */

/**
 * @typedef {object} TiledRow
 * @property {TiledItem[]} items -
 * @property {number} height -
 */

import moment from '@nextcloud/moment'

const BASE_ROW_HEIGHT = 200

/**
 * @param {TiledItem[]} items The list of items to display.
 * @param {number} containerWidth The width of the row.
 * @return {Object<string, TiledRow[]>}
 */
export function computeLayoutByMonth(items, containerWidth) {
	const itemsByMonth = splitItemsByMonths(items)

	return Object.keys(itemsByMonth).reduce(
		/**
		 * @param {Object<string, TiledRow[]>} layoutByMonth -
		 * @param {string} month -
		 */
		(layoutByMonth, month) => {
			return {
				...layoutByMonth,
				[month]: splitItemsInRows(itemsByMonth[month], containerWidth),
			}
		}, {})
}

/**
 * @param {TiledItem[]} items The list of items to split.
 * @return {Object<string, TiledItem[]>}
 */
function splitItemsByMonths(items) {
	return items.reduce(
		/**
		 * @param {Object<string, TiledItem[]>} itemsByMonth -
		 * @param {TiledItem} item -
		 */
		(itemsByMonth, item) => {
			const date = `${formatDate(item.injected.lastmod, 'YYYY')}-${formatDate(item.injected.lastmod, 'MMMM')}`

			return {
				...itemsByMonth,
				[date]: [...(itemsByMonth[date] ?? []), item],
			}
		},
		{}
	)
}

/**
 * @param {string} date The date as a string.
 * @param {string} format Date format to use (ie. 'YYYY', 'M', 'DD', ...).
 * @return {string}
 */
function formatDate(date, format) {
	return moment(date).format(format)
}

/**
 * Split items in rows of equal width.
 * The last row will not be forced to match containerWidth.
 *
 * @param {TiledItem[]} items The list of item to split in row of equal width.
 * @param {number} containerWidth The width of a row.
 * @return {TiledRow[]}
 */
function splitItemsInRows(items, containerWidth) {
	if (containerWidth === 0) {
		return []
	}

	const rows = []
	let rowNumber = 0

	while (items.length > 0) {
		/** @type { TiledItem[] } items */
		const rowItems = []

		// Fill the row with new items as long as the width is less than containerWidth.
		do {
			// @ts-ignore - We know that items.shift() is not undefined as we always check that items.length > 0.
			rowItems.push(items.shift())
		} while (items.length > 0 && computeRowWidth([...rowItems, items[items.length - 1]]) <= containerWidth)

		rows[rowNumber++] = {
			items: rowItems,
			height: computeRowHeight(rowItems, containerWidth, items.length === 0),
		}
	}

	return rows
}

/**
 * Compute the row width based on its items with the assumption that their height is BASE_ROW_HEIGHT.
 *
 * @param {TiledItem[]} items The list of items in the row.
 * @return {number} The width of the row
 */
function computeRowWidth(items) {
	return items
		.map(item => BASE_ROW_HEIGHT * (item.width / item.height))
		.reduce((sum, itemRatio) => sum + itemRatio)
}

/**
 * Compute the row height based on its items and on the container's width.
 *
 * Math time !
 * With Rn the aspect ratio of item n
 *      Wn the width of item n
 *      Hn the height of item n
 *      Wc the width of the container
 *      Hr the height of the row
 * For n items we want: Wc = W1 + W2 + ... + Wn
 * We know Rn = Wn / Hn
 * So Wn = Rn * Hn
 * So Wc = (R1 * H1) + (R2 * H2) + ... + (Rn * Hn)
 * But we also want Hr === H1 === H2 === ... === Hn
 * So Wc = (R1 * Hr) + (R2 * Hr) + ... + (Rn * Hr)
 * So Wc = Hr * (R1 + R2 + ... + Rn)
 * So Hr = Wc / (R1 + R2 + ... + Rn)
 *
 * @param {TiledItem[]} items The list of items in the row.
 * @param {number} containerWidth The width of the row.
 * @param {boolean} isLastRow Whether we are computing the height for the last row.
 * @return {number} The height of the row
 */
function computeRowHeight(items, containerWidth, isLastRow) {
	const sumOfItemsRatio = items
		.map(item => (item.width / item.height))
		.reduce((sum, itemRatio) => sum + itemRatio)

	let rowHeight = containerWidth / sumOfItemsRatio

	// Exception 1: there is only one item which is larger than containerWidth.
	// Limit its height so that itemWidth === containerWidth
	if (items.length === 1 && items[0].width > containerWidth) {
		rowHeight = containerWidth / (items[0].width / items[0].height)
	}

	// Exception 2: we reached the last row.
	// Do force the items width to match containerWidth, and limit their heigh to BASE_ROW_HEIGHT.
	if (isLastRow) {
		rowHeight = Math.min(BASE_ROW_HEIGHT, rowHeight)
	}

	return rowHeight
}
