<template>
	<div>
		<PhotosHeader :selection="selection" />

		<div ref="container" class="items-container">
			<div v-for="(itemsForMonth, month) of layoutByMonth"
				:key="month">
				<h3 class="section-header">
					<b>{{ month.split('-')[1] }}</b> {{ month.split('-')[0] }}
				</h3>

				<div v-for="(row, index) of itemsForMonth"
					:key="index"
					class="items-row"
					:style="{height: `${row.height}px`}">
					<component :is="item.renderComponent"
						v-for="item of row.items"
						:key="item.fileid"
						:item="item"
						:selected="checkedItems[item.injected.fileid] === true"
						:style="{ width: `${row.height * item.width / item.height}px`, height: `${row.height}px`}"
						@select-toggled="onItemSelectToggle" />
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import PhotosHeader from '../components/PhotosHeader.vue'
import { computeLayoutByMonth } from '../services/TiledLayout.js'

export default {
	name: 'TiledLayout',
	components: {
		PhotosHeader,
	},
	props: {
		/** @type {Array<TiledRow>} */
		items: {
			type: Array,
			required: true,
		},
		updateFunction: {
			type: Function,
			required: true,
		},
		loader: {
			type: Object,
			required: true,
		},
	},
	data() {
		return {
			/** @type {number} */
			containerWidth: 0,
			/** @type {Object<number, boolean>} */
			checkedItems: {},
		}
	},
	computed: {
		selection() {
			return Object.keys(this.checkedItems).filter(fileId => this.checkedItems[fileId])
		},

		layoutByMonth() {
			return computeLayoutByMonth(this.items, this.containerWidth)
		},

	},
	mounted() {
		this.updateFunction()
		this.containerWidth = this.$refs.container.clientWidth ?? 0
		window.addEventListener('resize', this.updateContainerWidth)
	},
	beforeUnmount() {
		window.removeEventListener('resize', this.updateContainerWidth)
	},
	methods: {
		/**
		 * @param {Event} event
		 */
		updateContainerWidth() {
			this.containerWidth = this.$refs.container.clientWidth ?? 0
		},

		onItemSelectToggle({ fileid, value }) {
			this.$set(this.checkedItems, fileid, value)
		},
	},
}
</script>

<style lang="scss">
.items-row {
	display: flex;
	justify-content: space-around;
	width: fit-content; // Prevent solitary image to be rendered in the middle because of the flex layout.

	// Month and year.
	.section-header {
		margin: 16px 0;
	}
}

</style>
